import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '../App.vue'

import Chats from '../components/Chats.vue'
import Friends from '../components/Friends.vue'
import Find from '../components/Find.vue'
import More from '../components/More.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'App',
    component: App
  },
  { path: '/', component: App, redirect: '/friends' },
  { path: '/friends', component: Friends, name: 'friends' },
  { path: '/chats', component: Chats, name: 'chats' },
  { path: '/find', component: Find, name: 'find' },
  { path: '/more', component: More, name: 'more' },
  { path: '/profile', component: More, name: 'Profile' },
  { path: '/chating', component: More, name: 'Chating' }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'tab_selected',
  linkExactActiveClass: 'tab-bar_tab'
})

export default router
