          <i class="fa fa-fighter-jet"></i>
          <i class="fa fa-wifi"></i>
        </div>

        <div class="header_center header_column">
          <span>18:20</span>
        </div>

        <div class="header_right header_column">
          <i class="fa fa-moon-o"></i>
          <i class="fa fa-bluetooth-b"></i>
          <span class="header__battery">
            100%
            <i class="fa fa-battery-full"></i>
          </span>
        </div>
      </div>

      <div class="header_bottom">
        <div class="header_left header_column">
          <span class="menu">{{menu}}</span>
        </div>

        <div class="header_center header_column">
          <p>
            {{title}}
            <span class="yellow_Num">{{num}}</span>
          </p>
        </div>
        <div class="header_right header_column">
          <i :class="icon"></i>
